<template>
    <div class="cell" :class="cellClasses">
        <slot></slot>
    </div>
</template>

<script>
export default {
    props: ["pointer"],
    computed: {
        cellClasses() {
            return {
                pointer: this.pointer,
            };
        },
    },
};
</script>

<style scoped>
.cell {
    width: 98px;
    height: 98px;
    border: 1px solid black;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 40px;
}

.pointer:hover {
    cursor: pointer;
}
</style>
