<template>
    <div class="scorecard">
        (X) P1 {{ points.user1 }} - {{ points.user2 }} P2 (O)
    </div>
    <game-board :single-player="false" @game-over="gameOver"></game-board>
</template>

<script>
import GameBoard from "../components/game/GameBoard.vue";

export default {
    components: {
        GameBoard,
    },
    data() {
        return {
            points: {
                user1: 0,
                user2: 0,
            },
        };
    },
    methods: {
        gameOver(winner) {
            if (winner == "X") {
                this.points.user1++;
            } else if (winner == "O") {
                this.points.user2++;
            } else {
                this.points.user1 += 0.5;
                this.points.user2 += 0.5;
            }
        },
    },
};
</script>

<style scoped>
.scorecard {
    font-size: 20px;
}
</style>
